nav.custom-navbar-menu {
    margin: 20px auto;
    background: #f2f2f2;
    border-radius: 2px; 
    ul{
        list-style: none;
        padding: 0;

        li{
            &:after{
                content: "/";
                padding: 5px 5px 5px 20px;
            }
            display: inline-block;
            padding: 15px 10px;
            a{
                text-transform: uppercase;
            }
            a.active{
                color: #FF5722;
            }
            a:hover,a:active,a:focus{
                text-decoration: none;
            }
        }
    }
}
.card-box{
    background: #f6f6fa;
    padding: 20px;
    border-radius: 2px;
}
